import React from './src/react';
import ReactDOM from './src/react-dom';

class App extends React.Component {
    render() { 
        return <h1>Hello World</h1>;
    }
}

class Conuter extends React.Component {
    constructor( props ) {
        super( props );
        this.state = {
            num: 0
        }
    }
    componentWillUpdate() {
        // console.log( 'update' );
    }

    componentWillMount() {
        // console.log( 'mount' );
    }

    onClick() {
        this.setState( { num: this.state.num + 1 } );
    }

    render() { 
        // transform-react-jsxÊòØÂ∞ÜjsxËΩ¨Êç¢‰∏∫jsÁöÑbabelÊèí‰ª∂
        // ÂÆÉÊúâ‰∏Ä‰∏™pragmaÈ°πÔºåÂèØ‰ª•ÂÆö‰πâjsxËΩ¨Êç¢ÊñπÊ≥ïÁöÑÂêçÁß∞„ÄÇ
        // È¶ñÂÖàjsxÁâáÊÆµ‰ºöË¢´ËΩ¨ËØëÊàêÁî®React.createElementÊñπÊ≥ïÂåÖË£πÁöÑ‰ª£Á†Å
        return (  
            <div onClick={ () => this.onClick() }>
                Hello World
                <h1>number: {this.state.num}</h1>
                <button>add</button>
            </div>
        );
    }
}

ReactDOM.render(<Conuter />, document.getElementById('root'));