import { renderComponent } from '../react-dom/diff';
class Component {
    // ÈÄöËøáÁªßÊâøReact.ComponentÂÆö‰πâÁöÑÁªÑ‰ª∂ÊúâËá™Â∑±ÁöÑÁßÅÊúâÁä∂ÊÄÅstateÂèØ‰ª•ÈÄöËøáthis.stateËé∑ÂèñÂà∞
    // ÂêåÊó∂‰πüËÉΩÈÄöËøáthis.propsÊù•Ëé∑Âèñ‰º†ÂÖ•ÁöÑÊï∞ÊçÆ
    // ÊâÄ‰ª•Âú®ÊûÑÈÄ†ÂáΩÊï∞‰∏≠ÔºåÈúÄË¶ÅÂàùÂßãÂåñstateÂíåprops
    constructor(props = {}) {
        this.state = {};
        this.props = props;
    }
    // ÁªÑ‰ª∂ÂÜÖÈÉ®ÁöÑstateÂíåÊ∏≤ÊüìÁõ∏ÂÖ≥ÔºåstateÊîπÂèòÊó∂ÈÄöÂ∏∏‰ºöÂá∫ÂèëÊ∏≤ÊüìÔºå‰∏∫‰∫ÜËÆ©react
    // Áõ¥Âà∞Êàë‰ª¨ÊîπÂèò‰∫ÜstateÔºåÊàë‰ª¨Âè™ËÉΩÈÄöËøásetStateÊñπÊ≥ï‰øÆÊîπÊï∞ÊçÆ
    setState(stateChange) {
        // Â∞Ü‰øÆÊîπÂêàÂπ∂Âà∞state
        Object.assign(this.state, stateChange);
        // Âú®ÊØèÊ¨°Êõ¥Êñ∞stateÂêéÔºåÈúÄË¶ÅË∞ÉÁî®renderComponentÊñπÊ≥ïÈáçÊñ∞Ê∏≤ÊüìÁªÑ‰ª∂
        renderComponent(this);
    }
}
export default Component;